import "./Hero.css";
import { Fade } from "react-awesome-reveal";

const Hero = ({ onClickOpenModal }) => {
  return (
    <section className="hero" id="hero">
      <div className="left-box">
        <Fade cascade triggerOnce>
          <h1>Raqamli texnologiyalar o’quv markazi</h1>
          <p>
            Bizda o’qib zamonaviy kasblarni o’rganing va ilk millionlaringizni
            ishlashni boshlang. Sizda qiziqish paydo bo’lgan bo’lsa
            kurslarimizga yoziling
          </p>
          <button
            type="button"
            className="show-modal"
            onClick={() => onClickOpenModal()}
          >
            Kursga yozilish
          </button>
        </Fade>
      </div>
    </section>
  );
};

export default Hero;
